import React, { Component, Suspense } from "react";
import {
  // BrowserRouter as Router,
  HashRouter,
  Switch,
  Route,
  Redirect
} from "react-router-dom";

import Layout from "./HOC/Layout/Layout";
import List from './components/List/List';
import WordsConstructor from "./containers/WordsConstructor/WordsConstructor";
import OppositeWords from './containers/OppositeWords/OppositeWords';

import Auth from "./containers/Auth/Auth";
import Logout from "./containers/Auth/Logout/Logout";

import Rites from './components/Texts/RitesOfPassage';
import Video from './components/Video/Video';

const SynonymsAsync = React.lazy(()=> import('./containers/OppositeWords/OppositeWords'));
const IdiomsAsync = React.lazy(() => import('./containers/Idioms/Idioms'));
const DragAndDropAsync = React.lazy(() => import('./containers/DragAndDrop/DragAndDrop'));
const FillGapsAsync = React.lazy(() => import('./containers/FillGaps/FillGaps'));

class App extends Component {
  render() {
    let routes = null;
    let isAuthenticated = localStorage.getItem("token");
    if (isAuthenticated) {
      // routes = <Switch />;
    } else {
      routes = (
        <Switch>
          <Route path="/auth" component={Auth} />
          <Route path="/logout" component={Logout} />
          <Route path="/texts" render={() => <List content={[{desc: 'Rites of Passage', url: '/rites-of-passage'}]} key='1' />} />
          <Route path="/synonyms" render={() => <List content={[
            {desc: 'Unit 1', url: '/unit1'},
            {desc: 'Unit 2', url: '/unit2'},
            {desc: 'Unit 3', url: '/unit3'}
          ]} key='2' />} />
          <Route path='/videos' render={()=> <List content={[{desc: 'How to make faster decisions', url: '/how-to-make-faster-decisions'}, {desc: 'Why working from home is good for business', url: '/why-working-from-home-is-good-for-business'},
          {desc: '3 ways how to measure your adaptability - and how to improve it', url: '/how-to-measure-your-adaptability'}]} key='3' />}/>
          <Route path="/how-to-make-faster-decisions" render={() => <Video url='https://embed.ted.com/talks/lang/en/patrick_mcginnis_how_to_make_faster_decisions' desc='How to make faster decisions' quotes key='video1'/>} />
          <Route path="/why-working-from-home-is-good-for-business" render={() => <Video url='https://embed.ted.com/talks/matt_mullenweg_why_working_from_home_is_good_for_business' desc='Why working from home is good for business' taskLink='/drag-and-drop#10' mainTask='FillGaps' key='video2' />} />
          <Route path='/how-to-measure-your-adaptability' render={() => <Video url='https://embed.ted.com/talks/natalie_fratto_3_ways_to_measure_your_adaptability_and_how_to_improve_it' key='video3' taskLink='/drag-and-drop#12' />}/>
          <Route path='/idioms' render={() => <IdiomsAsync/>} />
          <Route path="/logout" component={Logout} />
          <Route exact path='/rites-of-passage'component={Rites}/>
          <Route exact path="/unit1" render={() => <OppositeWords page='4' />} key='1'/>
          <Route exact path="/unit2" render={() => <SynonymsAsync page='3'/>} key='2'/>
          <Route exact path="/unit3" render={() => <OppositeWords page='5' />} key='3'/>
          <Route exact path='/how-to-make-faster-decisions-task' render={() => <SynonymsAsync page='8' key='6'/>} />
          <Route exact path="/opposite-words" render={() => <SynonymsAsync page='2'/>} key='4'/>
          <Route exact path="/rites-of-passage/task-1" render={() => <SynonymsAsync page='6'/>} key='5'/>
          <Route exact path="/" component={WordsConstructor} />
          <Route exact path='/drag-and-drop' component={DragAndDropAsync} key='drag1'/>
          <Route exact path='/fill-gaps' component={FillGapsAsync} />
          <Redirect to="/" />
        </Switch>
      );
    }
    return (
      <HashRouter basename='/english'>
        <Layout><Suspense fallback={<div>Loading...</div>}>{routes}</Suspense></Layout>
      </HashRouter>
    );
  }
}

export default App;



